@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-box {
  margin-bottom: 30px;
}
.search-box > input {
  background: #F5F6F7;
  border: 0;
  border-radius: 8px;
  font-size: 12px;
  width: 250px;
  padding: 8px 15px;
}
.search-box > input::-webkit-input-placeholder {
  color: #C6C8CC;
}
.search-box > input:-ms-input-placeholder {
  color: #C6C8CC;
}
.search-box > input::-ms-input-placeholder {
  color: #C6C8CC;
}
.search-box > input::placeholder {
  color: #C6C8CC;
}

.cities-list {
  width: 260px;
  font-size: 14px;
  color: #565D66;
  padding: 8px 10px;
  position: absolute;
  background: #FFFFFF;
}

.cities-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.cities-list ul li {
  margin: 0;
  padding: 5px;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-bottom: 1px solid #DEE0E2;
}

.cities-list ul li:last-of-type {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.cities-list ul li:hover {
  background: #F5F6F7;
}

.entry-details {
  padding: 5px;
  width: 30%;
  font-size: 14px;
  display: inline-flex;
  flex-direction: row;
  align-items: center;
}

.entry-details > span {
  background-color: #F5F6F7;
  border-radius: 50%;
  display: inline-block;
  font-weight: bold;
  height: 15px;
  margin-right: 10px;
  padding: 10px;
  text-align: center;
  width: 15px;
}

.entry-details__place > p, .entry-details__time > p {
  margin: 0;
}

.entry-details__time {
  margin-left: auto;
}

.entry-details__time > p {
  text-align: right;
}

.entry-details__place > .title, .entry-details__time > .title {
  color: #565D66;
  font-weight: bold;
}

.entry-details__place > .subtitle, .entry-details__time > .subtitle {
  color: #82878E;
  font-size: 12px;
}


.entry-actions {
  cursor: pointer;
  display: inline-flex;
  flex-direction: column;
  font-size: 12px;
  justify-content: center;
  margin: 0 10px;
}

.entry-times {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  width: 63%;
  margin-left: 10px;
}

.entry-times > ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  list-style-image: none;
  display: flex;
  flex-direction: row;
}

.entry-times > ul li {
  color: #565D66;
  font-size: 14px;
  font-weight: 500;
  height: 32px;
  text-align: center;
  padding: 7px;
  width: 15px;
}

.entry-times > ul li.light-blue {
  background: #DFEDFD;
}

.entry-times > ul li.gray {
  background: #F5F6F7;
}

.entry-times > ul li.dark-blue {
  background: #343D47;
  color: #FFFFFF;
}

.entry-times > ul li.end {
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
}

.entry-times > ul li.start {
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  margin-left: 2px;
  padding: 7px 5px;
}

.entry-times > ul li.current-time {
  background: #F3CEBA;
  color: #565D66;
}

.entry-times > ul li > span {
  font-size: 10px;
}

.entry {
  display: flex;
  margin-bottom: 20px;
}

.world-time-widget {
  background: #FFFFFF;
  border-radius: 8px;
  font-family: 'Roboto', sans-serif;
  margin: 50px auto;
  padding: 15px 10px;
  width: 1028px;
}

body {
  background: #F4F4F4;
}

